import React, { useRef } from 'react';
import { dateConvert, originalImgPath, smallImgPath } from '../../global/config';
import Modal from '../Modal';
const DetailsHead = React.forwardRef(({ match, details, setData, setTypeActionCreator, setLists, accoundId, lists, accessToken, setAllListsDetails, allListsDeails, addToList, cleanAllListsDetailsAc, addToListHandler, showListsHandler, textEnding, goBackHandler, setModalActive }, addToListRef) => {
    return (
        <article className="details-head">
            <div className="details-head__bg">
                <img src={`${originalImgPath}${details.backdrop_path}`} alt="" />
            </div>
            <div className="container-fluid">
                <div className="details-wrap">
                    <div className="details-info">
                        <div className="details-networks">
                            {details.networks && details.networks.map((n, id) => <div key={id} className="details-networks__item"><img src={`${smallImgPath}${n.logo_path}`} alt={n.name} /></div>)}
                        </div>
                        <h2 className="title">{details.name || details.title}</h2>
                        <div className="details-metadata">
                            {details.first_air_date && <div className="details-metadata__item">{details.first_air_date.substr(0, 4)}</div>}
                            {details.release_date && <div className="details-metadata__item">{details.release_date.substr(0, 4)}</div>}
                            {( (details.releases && details.releases.countries.find(item => item.iso_3166_1 === 'US') &&  details.releases.countries.find(item => item.iso_3166_1 === 'US').certification!=="")  || details.content_ratings) &&
                                <div className="details-metadata__item">
                                    <span className="rating">
                                        {((details.releases &&  details.releases.countries.find(item => item.iso_3166_1 === 'US') && details.releases.countries.find(item => item.iso_3166_1 === 'US').certification)
                                            || (details.content_ratings && details.content_ratings.results.length > 0 && details.content_ratings.results[0].rating)
                                        )}
                                    </span>
                                </div>
                            }
                            {(details.runtime && details.runtime>0) ? <div className="details-metadata__item">{details.runtime} мин</div>: ''}
                            {details.number_of_seasons &&
                                <div className="details-metadata__item">{details.number_of_seasons + ` сезон` + textEnding(details.number_of_seasons)}</div>}
                            <div className="details-metadata__item genres">{details.genres.map((g, id) => <span key={id}>{g.name}</span>)}</div>
                        </div>
                        <div className="details-overview">{details.overview}</div>
                        {details.in_production && details.last_episode_to_air  && <div className="details-credits">Последняя вышедшая серия:
                            <div  className="details-credits__item">{details.last_episode_to_air.episode_number + ' серия '+details.last_episode_to_air.season_number + ' сезона:'} { dateConvert(details.last_episode_to_air.air_date) }</div>
                        </div>}
                        {details.in_production && details.next_episode_to_air  && <div className="details-credits">Следующая серия:
                            <div  className="details-credits__item"> {details.next_episode_to_air.episode_number} серия {details.next_episode_to_air.season_number} сезона: { dateConvert(details.next_episode_to_air.air_date) }  </div>
                        </div>}
                        {details.credits.cast && details.credits.cast.length > 1 && <div className="details-credits">В ролях:
                            {details.credits.cast.map((c, id) => id < 5 && <div key={id} className="details-credits__item">{c.name}</div>)}
                        </div>}
                        {details.credits.crew && details.credits.crew.length > 0 && <div className="details-credits">Создатели:
                            {details.credits.crew.map((c, id) => id < 2 && <div key={id} className="details-credits__item">{c.name}</div>)}
                        </div>}
                        <div className="details-btns">
                            <button className="btn" onClick={setModalActive}>Смотреть трейлер</button>
                            {accessToken && lists && lists.length > 0 &&
                                <div className="addToLists" ref={addToListRef} onClick={(e) => showListsHandler(e)}>
                                    <button className="btn">Добавить в список</button>
                                    <ul className="lists">
                                        {lists.map(l =>
                                            <li key={l.id}
                                                className={`${allListsDeails.find(item => item.id === l.id) && allListsDeails.find(item => item.id === l.id).results.some(el => el.id === details.id) ? 'is-ex' : ''}`}
                                                onClick={(e) => addToListHandler(e, l.id)}
                                            >
                                                {l.name + `(${l.number_of_items})`}
                                                {allListsDeails.find(item => item.id === l.id) && allListsDeails.find(item => item.id === l.id).results.some(el => el.id === details.id) && <span>(Уже в этом списке)</span>}
                                            </li>)
                                        }
                                    </ul>
                                </div>
                            }
                        </div>

                        {/* <button className="btn" onClick={goBackHandler}>Back</button> */}
                    </div>
                    <div className="details-img"></div>
                </div>
            </div>
            <TrailerModal details={details} />
        </article>
    );
});
const TrailerModal = props => {
    const item = props.details
    const iframeRef = useRef(null)

    const onClose = () => {
        if (item.videos.results.length > 0) {
            iframeRef.current.setAttribute('src', '')
            iframeRef.current.setAttribute('title', '')
        }
    }
    return (
        <Modal active={false} id={`modal__${item.id}`} onClose={onClose}>
            <div className="box">
                <h3 className="title middle">{item.videos.results.length > 0 ? item.videos.results[item.videos.results.length - 1].name : "Няма"}</h3>
                {
                    item.videos.results.length > 0 &&
                    <div className="modal__video">
                        <iframe ref={iframeRef} src="" title="%" frameBorder="0" width="100%" height="100%"></iframe>
                    </div>
                }

            </div>
        </Modal>
    )

}
export default DetailsHead;