import { connect } from 'react-redux';
import { compose } from 'redux';
import { withRouter, Redirect } from 'react-router';
import { setBodyOverflow } from "../redux/app-reducer";
import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types'
const Modal = props => {
    const contentRef = useRef(null)
    const closeModal = (e) => {
        if (e.target.classList.contains('modal') || e.target.classList.contains('modal__close')) {
            contentRef.current.parentNode.classList.remove('active')
            props.setBodyOverflow(false)
            if (props.onClose) props.onClose()
        }

    }
    const [active, setActive] = useState(false)
    useEffect(() => {
        setActive(props.overflow)
    }, [props.active])
    useEffect(() => {
        if (props.bodyOverflow) {
            document.body.style.overflow = "hidden"
        } else {
            document.body.style.overflow = ""
        }
    }, [props.bodyOverflow])
    return (
        <div id={props.id} className={`modal ${active ? 'active' : ''}`} onClick={closeModal}>
            <div ref={contentRef} className={`modal-content ${props.className}`}>
                {props.children}
            </div>
            <div className="modal__close" >
                <svg viewBox="0 0 20 20" focusable="true"><path d="M10.5 9.3L1.8 0.5 0.5 1.8 9.3 10.5 0.5 19.3 1.8 20.5 10.5 11.8 19.3 20.5 20.5 19.3 11.8 10.5 20.5 1.8 19.3 0.5 10.5 9.3Z"></path></svg>
            </div>
        </div>
    );
};

// Modal.propTypes = {
//     active: PropTypes.bool,
//     id: PropTypes.string
// }

// export const ModalContent = props => {

//     return (
//         <div ref={contentRef} className="modal-content">
//             {props.children}
//             <div className="modal__close" onClick={closeModal}>x</div>
//         </div>
//     )
// }
// ModalContent.propTypes = {
//     onClose: PropTypes.func,
// }
const mapStateToProps = (state) => {
    return {
        bodyOverflow: state.app.bodyOverflow,
    }
}
export default compose(
    connect(mapStateToProps, { setBodyOverflow }),
    withRouter,
)(Modal)