                // <article className="details-slider">
                //     <div className="container-fluid">
                //         <h3 className="title middle">Реокмендации</h3>
                //         <div className="details-slider__wrap">
                //             <Swiper
                //                 spaceBetween={15}
                //                 slidesPerView={'auto'}
                //                 navigation
                //                 className="details-slider__container"
                //             >
                //                 {details.recommendations.results.map((item, i) =>
                //                     <SwiperSlide key={i} className="details-slider__item small">
                //                         <Card src={item.poster_path} title={item.title} type={item.media_type} titleId={item.id} score={item.vote_average.toFixed(1)} clickHandler={() => updateHandler(item.id, item.media_type)}/>
                //                     </SwiperSlide>)
                //                 }
                //             </Swiper>

                //         </div>
                //     </div>
                // </article>
