import React, { useEffect, useRef, useState } from 'react';
// import { middleImgPath } from '../../global/config';
// import SwiperCore, { Navigation } from 'swiper/core';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper-bundle.min.css'
import 'swiper/swiper.min.css'
const SliderSection = (props) => {
    const [swiper, setSwiper] = useState(null);
    useEffect(()=>{
        if(props.toSlide){
            if (swiper) {
                swiper.slideTo(0);
                props.setToSlide(false)
            }
        }
        
    },[swiper, props.toSlide])
    return (
        <>
        {
            <article className="details-slider">
                <div className="container-fluid">
                    <h3 className="title middle">{props.title}</h3>
                    <div className="details-slider__wrap">
                        <Swiper
                            spaceBetween={15}
                            slidesPerView={'auto'}
                            navigation
                            className="details-slider__container"
                            onSwiper={(s) => {
                                // console.log("initialize swiper", s);
                                setSwiper(s);
                              }}
                        >
                            {props.children}
                        </Swiper>
                    </div>
                </div>
                {/* <button onClick={()=>{swiper.slideTo(2)}}>go</button> */}
            </article>
        }
    </>
    );
};

export default SliderSection;