import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { withRouter, Redirect } from 'react-router';
import { useHistory } from "react-router-dom"
import {  middleImgPath } from '../global/config';
import { setData, setTypeActionCreator } from "../redux/details-reducer";
import { setLists, setAllListsDetails, addToList, cleanAllListsDetailsAc } from "../redux/lists-reducer";
import { setBodyOverflow } from "../redux/app-reducer";
import { useRef } from 'react';
import DetailsHead from '../components/Details/DetailsHead';
import * as axios from "axios"
import Cookie from 'universal-cookie';
import { instanceV4 } from '../api/api';
import DetailsImages from '../components/Details/DetailsImages';
import DetailsRecomendation from '../components/Details/DetailsRecomendation';
const cookie = new Cookie();
const DetailsPage = ({ match, details, setData, setTypeActionCreator, setLists, accoundId, lists, accessToken, setAllListsDetails, allListsDeails, addToList, cleanAllListsDetailsAc, setBodyOverflow }) => {
	const history = useHistory()
	const textEnding = (num) => {
		if (num % 10 >= 2 && num % 10 <= 4) {
			return 'а'
		} else if ((num % 10 >= 5 && num % 10 <= 9) || (num % 10 === 0 && num !== 1)) {
			return 'ов'
		} else {
			return ''
		}
	}
	const [toSlide, setToSlide] = useState(false)
	const setDataHandler = (id, type, scolltoTop=false)=>{
		setData(id, type)
		setTypeActionCreator(type)
		if(scolltoTop){
			document.querySelector('.details').scrollIntoView({ 
			// behavior: 'smooth', 
			block: 'start' })
			setToSlide(true)
		}
	}
	useEffect(() => {
		setDataHandler(match.params.titleId, match.params.titleType)
	}, [])
	const getAllListDetailsHandler = () => {
		// cleanAllListsDetailsAc()
		lists && lists.length > 0 && lists.forEach(list => {
			if ((allListsDeails.length < 1) || (!allListsDeails.some(l => l.id === list.id))) {
				
				setAllListsDetails(list.id)
			}
		})
	}
	useEffect(() => {
		if (details) {
			document.title = details.name ?? details.title
		}
	}, [details])
	useEffect(() => {
		if (accessToken) {
			getAllListDetailsHandler()
		}
	}, [lists])
	useEffect(() => {
		if (accessToken) {
			setLists(accoundId)
		}
	}, [accoundId])
	const addToListRef = useRef(null)
	const  addToListHandler = async (e, id) => {
		// debugger
		// console.log(instanceV4.defaults.headers['Authorization'])   
		// if (instanceV4.defaults.headers['Authorization'] !== `Bearer ${cookie.get('access_token')}`) {
		// 	instanceV4.defaults.headers['Authorization'] = `Bearer ${cookie.get('access_token')}`;
		// }	
		// cleanAllListsDetailsAc()
		addToList(id, match.params.titleType, details.id, accoundId);
		setTimeout(function(){
			// getAllListDetailsHandler()
			// setLists(accoundId)
			// console.log('sds223')
		}, 2000)
	}
	const showListsHandler = (e) => {
		if (!addToListRef.current.classList.contains('is-active')) {
			addToListRef.current.classList.add('is-active')
		} else if (!e.target.classList.contains('is-ex')) {
			addToListRef.current.classList.remove('is-active')
		}
	}
	const goBackHandler = () => {
		history.goBack()
	}
	const setModalActive = ()=>{
		const modal = document.querySelector(`#modal__${details.id}`)
		modal.classList.toggle('active')
		setBodyOverflow(true)
		if(details.videos.results.length>0){
			modal.querySelector('iframe').setAttribute('src', `https://www.youtube.com/embed/${details.videos.results[details.videos.results.length-1].key}`)
			modal.querySelector('iframe').setAttribute('title', details.videos.results[details.videos.results.length-1].name)
		}	
	}
	const setImageModalActive = ()=>{
		const modal = document.querySelector(`#modal-image`)
		modal.classList.toggle('active')
		setBodyOverflow(true)	
	}
	const [locationKeys, setLocationKeys] = useState([])
	// Обработка нажатий кнопок вперед и назад в браузере
	useEffect(() => {
		return history.listen(location => {
			if (history.action === 'PUSH') {
				setLocationKeys([location.key])
			}
			if (history.action === 'POP') {
				if (locationKeys[1] === location.key) {
					setLocationKeys(([_, ...keys]) => keys)
					// Handle forward event					
					if(location.pathname.indexOf('title')===1){
						location.pathname && location.pathname.indexOf('title') && setDataHandler(location.pathname.split('/title/')[1].split('/')[1], location.pathname.split('/title/')[1].split('/')[0], true)
					}
				} else {
					setLocationKeys((keys) => [location.key, ...keys])
					// Handle back event	
					if(location.pathname.indexOf('title')===1){
						location.pathname && location.pathname.indexOf('title') && setDataHandler(location.pathname.split('/title/')[1].split('/')[1], location.pathname.split('/title/')[1].split('/')[0], true)
					}					
				}
			}
		})
	}, [locationKeys, history.location.pathname])
	return (
		details !== null &&
		<section className="details" id={`detais-${details.id}`}>
			<DetailsHead match={match} details={details} setData={setData} setTypeActionCreator={setTypeActionCreator} setLists={setLists} accoundId={accoundId} lists={lists} accessToken={accessToken} getAllListDetailsHandler={getAllListDetailsHandler} allListsDeails={allListsDeails} addToList={addToList} addToListHandler={addToListHandler} ref={addToListRef} textEnding={textEnding} showListsHandler={showListsHandler} goBackHandler={goBackHandler} setModalActive={setModalActive} />
			<DetailsImages details={details} setModalActive={setImageModalActive} toSlide={toSlide} setToSlide={setToSlide}/>
			<DetailsRecomendation details={details} setDataHandler={setDataHandler} toSlide={toSlide} setToSlide={setToSlide}/>
		</section>
	);
};
const mapStateToProps = (state) => {
	return {
		details: state.details.data,
		type: state.details.titleType,
		accessToken: state.auth.accessToken,
		accoundId: state.auth.accountId,
		lists: state.lists.lists,
		allListsDeails: state.lists.allListsDeails
	}
}
export default compose(
	connect(mapStateToProps, { setData, setTypeActionCreator, setLists, setAllListsDetails, addToList, cleanAllListsDetailsAc, setBodyOverflow }),
	withRouter,
)(DetailsPage)