import React, { useRef, useState } from 'react';
import { originalImgPath, middleImgPath } from '../../global/config';
import SwiperCore, { Navigation } from 'swiper/core';
import { Swiper, SwiperSlide } from 'swiper/react';
import Modal from '../Modal';
import 'swiper/swiper-bundle.min.css'
import 'swiper/swiper.min.css'
import SliderSection from './SliderSection';
SwiperCore.use([Navigation]);
const DetailsImages = ({ details, setModalActive, toSlide, setToSlide }) => {
    const [image, setImage] = useState({
        src: null,
        id: null,
    })
    const modalHandler = (e, item, i)=>{
        setImage({
            src: item.file_path,
            id: i
        })
        setModalActive()
    }
    const setNextImageHandler = ()=>{
        if(image.id+1>details.images.backdrops.length-1){
            setImage({
                src: details.images.backdrops[0].file_path,
                id: 0
            })
        }else{
            setImage({
                src: details.images.backdrops[image.id+1].file_path,
                id: image.id+1
            })
        }     
    }
    const setPrevImageHandler = ()=>{
        if(image.id-1<0){
            setImage({
                src: details.images.backdrops[details.images.backdrops.length-1].file_path,
                id: details.images.backdrops.length-1
            })
        }else{
            setImage({
                src: details.images.backdrops[image.id-1].file_path,
                id: image.id-1
            })
        }
       
    }
    const prevRef = useRef(null);
    const nextRef = useRef(null);
    return (
        <>
            {details.images && details.images.backdrops.length > 0 &&
                <SliderSection title={"Избражения"} toSlide={toSlide} setToSlide={setToSlide}>
                    {details.images.backdrops.map((item, i) => <SwiperSlide key={i} onClick={(e)=>modalHandler(e,item,i)} className="details-slider__item img"><img src={middleImgPath + item.file_path} alt="" /></SwiperSlide>)}
                </SliderSection>
                // <article className="details-slider">
                //     <div className="container-fluid">
                //         <h3 className="title middle">Избражения</h3>
                //         <div className="details-slider__wrap">
                //             <Swiper
                //                 // modules={[Navigation]}
                //                 spaceBetween={15}
                //                 slidesPerView={'auto'}
                //                 navigation
                //                 className="details-slider__container"
                //             // onSlideChange={() => console.log('slide change')}
                //             // onSwiper={(swiper) => console.log(swiper)}
                //             // eslint-disable-next-line react/jsx-no-duplicate-props
                //             // navigation={{
                //             //     prevEl: prevRef.current ? prevRef.current : undefined,
                //             //     nextEl: nextRef.current ? nextRef.current : undefined,
                //             // }}
                //             // onInit={(swiper) => {
                //             //     swiper.params.navigation.prevEl = prevRef.current;
                //             //     swiper.params.navigation.nextEl = nextRef.current;
                //             //     swiper.navigation.update();
                //             // }}
                //             >
                //                 {/* <div className="details-slider__nav">
                //                     <div ref={prevRef} className="slider-btn prev"></div>
                //                     <div ref={nextRef} className="slider-btn next"></div>
                //                 </div> */}
                //                 {details.images.backdrops.map((item, i) => <SwiperSlide key={i} className="details-slider__item"><img src={middleImgPath + item.file_path} alt="" /></SwiperSlide>)}
                //             </Swiper>
                //         </div>
                //     </div>
                // </article>
            }
        <ImageModal image={image} setImage={setImage} setNextImageHandler={setNextImageHandler} setPrevImageHandler={setPrevImageHandler} total={details.images.backdrops.length}/>
        </>
    );
};
const ImageModal = props => {
    const item = props.image
    const iframeRef = useRef(null)

    const onClose = () => {
        if (item.src) {
            // iframeRef.current.setAttribute('src', '')
            props.setImage({
                src: null,
                id: null,
            })
        }
    }
    return (
        <Modal active={false} id={`modal-image`} onClose={onClose} className={'modal_image'}>
            <div className="box">
                {
                    item.src &&
                    <div className="modal__img">
                        <img ref={iframeRef} src={originalImgPath + item.src} alt="" />
                    </div>
                }
            </div>
            <div className="modal__btn-prev" onClick={props.setPrevImageHandler}></div>
            <div className="modal__btn-next" onClick={props.setNextImageHandler}></div>
            <div className="modal__pagination">{item.id+1} из {props.total}</div>
        </Modal>
    )

}

export default DetailsImages;